import React, { Component } from 'react'
import Button from 'react-bootstrap/Button';
import Modal from "react-bootstrap/Modal";
import './style.css';

const initialState = {
    gender: '',
    userName: '',
    userEmail: '',
    userAddress: '',
    userNameErr: '',
    userEmailErr: '',
    genderErr: '',
}

class Index extends Component {

    constructor(props) {
        super(props)
    
        this.state = initialState;

        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleEmailChange = this.handleEmailChange.bind(this);
        this.handleAddressChange = this.handleAddressChange.bind(this);
        this.handleGender = this.handleGender.bind(this);
        this.formValidate = this.formValidate.bind(this);

        this.handleShow = this.handleShow.bind(this);
		this.handleClose = this.handleClose.bind(this);
    }

    handleNameChange = (event) => {
        this.setState({
            userName: event.target.value,
        })
    }
    handleEmailChange = (event) => {
        this.setState({
            userEmail: event.target.value,
        })
    }
    handleAddressChange = (event) => {
        this.setState({
            userAddress: event.target.value,
        })
    }
    handleGender = (event) => {
        this.setState({
            gender: event.target.value,
        })

    }

    handleClose = () => {
        this.setState({ show: false });
        this.setState(initialState);
	}

	handleShow = (event) => {
        const isValid = this.formValidate();
        if(isValid){
            this.setState({ show: true });
        }
        
        event.preventDefault();
	}

    formValidate = () => {
        let userNameErr = "";
        let userEmailErr = '';
        let genderErr = ''

        if(this.state.userName.trim().length < 3){
            userNameErr ='User name is too short'
        }
        if(!this.state.userName){
            userNameErr='User name cannot be empty'
        }
        
        const reg = /^\w+([\.-]?\w+)@\w+([\.-]?\w+)(\.\w{2,3})+$/;

        if(!this.state.userEmail || reg.test(this.state.userEmail) === false){
            userEmailErr = 'Email is not valid'
        }

        if(!this.state.gender){
            genderErr = 'Please select gender'
        }

        if (userEmailErr || userNameErr || genderErr){
            this.setState({userEmailErr, userNameErr, genderErr});
            return false
        }
        return true
        
     }




    render() {
        
        return (
            <div className='form_bgc'>
            <div className='container'>
                
                <form onSubmit={this.handleShow} className='form_wrapper'>
                    <h2 className='pepper_primary_color text-center pb-3'>User Details</h2>
                <div className="form-group">
                <label>UserName</label>
                <input  type="text"
                        value={this.state.userName} 
                        onChange={this.handleNameChange} 
                        className='form-control'
                        required
                /> 
                <span className='text-danger'><strong>{this.state.userNameErr}</strong></span>
                </div>
                 <div className="form-group">
                <label>Email</label>
                <input  
                        type='email'
                        value={this.state.userEmail} 
                        onChange={this.handleEmailChange}
                        className='form-control'
                        required
                /> 
                <span className='text-danger'><strong>{this.state.userEmailErr}</strong></span>                
                </div>
                <div className="form-group">
                <label>Address</label>
                <textarea 
                        className="form-control"
                        value={this.state.userAddress} 
                        onChange={this.handleAddressChange}
                        rows="3">
                </textarea>
              </div>

                <label className="form-check-label">Gender</label> <br />
                
                <div className="form-group form-check pl-5">
                    <input 
                        value= "male"
                        onChange={this.handleGender}
                        type="checkbox" 
                        className="form-check-input" /> 
                    <label className="form-check-label pr-5">Male</label>
                    <input 
                        value='female'
                        onChange={this.handleGender}
                        type="checkbox" 
                        className="form-check-input" />
                    <label className="form-check-label pr-5">Female</label>
                    <input 
                        value='other'
                        onChange={this.handleGender}
                        type="checkbox" 
                        className="form-check-input" />
                    <label className="form-check-label">Other</label>
                </div> 
                <div className='text-danger mt-2 mb-4'><strong>{this.state.genderErr}</strong></div>         
                
            <input type='submit' className='btn btn-pepper ' />                
            </form>




                
        <Modal show={this.state.show} 
                onHide={this.handleClose}
                aria-labelledby="contained-modal-title-vcenter" 
                centered
                className='form_modal'>
					
					<Modal.Body>
                        <h2 className='pepper_primary_color text-center pb-3'>User Details</h2>
                        <ul className='list-group user_list_details my-4'>
                            <li className='list-group-item list-group-item-info'>
                                <span className='title'>Name</span>
                                <span className='seperator'>:</span>{` ${this.state.userName}`} 
                            </li>
                            <li className='list-group-item list-group-item-info'>
                                <span className='title'>Email</span>
                                <span className='seperator'>:</span>{` ${this.state.userEmail}`} 
                            </li>
                            <li className='list-group-item list-group-item-info'>
                                <span className='title'>Address</span>
                                <span className='seperator'>:</span>{` ${this.state.userAddress}`} 
                            </li>
                            <li className='list-group-item list-group-item-info text-capitalize'>
                                <span className='title'>Gender</span>
                                <span className='seperator'>:</span>{` ${this.state.gender}`} 
                            </li>
                        </ul>
						<Button className='my-3 float-right' variant="danger" onClick={this.handleClose}>Close</Button>

                        
                    </Modal.Body>
				</Modal>



            </div>
            </div>
        )
    }
}

export default Index